.form_container {
  display: flex;
  flex-direction: column;

  border-radius: 10px;
  border: 1px solid rgba(16, 24, 40, 0.2);
  padding: 12px;
}

.title {
  color: #101828;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.2;

  margin-bottom: 8px;
}

.desc {
  margin-bottom: 12px;

  color: #475467;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.input {
  padding: 8px;
  border: none;
  border-radius: 10px;
  background: #f7f7f7;
}

.button {
  border-radius: 200px;
  background: #e44848;
  color: #fff;
  border: none;
  max-width: 90px;
  height: 40px;
}

@media screen and (min-width: 1440px) {
  .form_container {
    padding: 24px;
  }

  .input {
    padding: 18px;
  }

  .button {
    max-width: 130px;
    height: 56px;
    margin-top: 10px;
  }
}
